.element__image {
  min-width: 100%;
  min-height: 282px;
  max-height: 282px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  display: block;
  margin: 0;
  object-fit: cover;
  transition: 0.4s ease-in-out;
}

.element__image:hover {
  opacity: 0.9;
  cursor: pointer;
}
